import axios from "axios";

interface IReCaptcha {
  threshold?: number;
  secret: string;
  ssm?: string;
  useIP?: boolean;
}

const defaults = { threshold: 0.8, secret: "" };

const reCAPTCHAv3 = (opts: IReCaptcha) => {
  const options = { ...defaults, ...opts };

  const reCAPTCHAv3Before = async (request: any): Promise<any> => {
    let verified = false;
    let score = 0;
    let ipSource = "";

    console.log("Secret: ", options.secret);

    if (options.secret.length && request.event?.body?.token) {
      verified = false;
    } else {
      await axios({
        method: "POST",
        url: "https://www.google.com/recaptcha/api/siteverify",
        params: {
          secret: options.secret,
          response: request.event?.body?.token,
          remoteip: request.event?.requestContext?.identity?.sourceIp,
        },
      })
        .then((response: any) => {
          if (response.status === 200) {
            if (response.data.success) {
              if (response.data.score >= options.threshold) {
                verified = true;
                score = response.data.score;
                options.useIP
                  ? (ipSource =
                      request.event?.requestContext?.identity?.sourceIp)
                  : null;
              }
            }
          }
        })
        .catch((error: Error) => {
          console.error(error);
        });
    }

    request.event = {
      ...request.event,
      state: {
        verified: true,
        reCaptcha: {
          score: score,
          ip: ipSource,
        },
      },
    };

    if (!request.event.state.ok) {
      return {
        statusCode: 401,
      };
    }
  };
  const reCAPTCHAv3OnError = async (request: any) => {
    console.error(request);
  };

  return {
    before: reCAPTCHAv3Before,
    onError: reCAPTCHAv3OnError,
  };
};

export default reCAPTCHAv3;
